:global body {
  background-color: #F0F0F0;
  overflow-x: hidden; }

.div {
  padding: 20px;
  align-content: center !important; }

@media only screen and (min-width: 768px) {
  .div {
    position: absolute;
    top: 15%;
    right: 5%;
    width: auto;
    height: auto;
    padding-left: 50px;
    padding-right: 50px;
    background-color: #FFF;
    border-radius: 20px; } }

@media only screen and (min-width: 478px) and (max-width: 767px) {
  .div {
    position: relative;
    width: 100%;
    height: auto;
    background-color: #FFF;
    text-align: center; }
    .div .form {
      text-align: center;
      width: 100%; }
      .div .form .SignUpButton {
        height: 40px;
        width: 100%; } }

@media only screen and (min-width: 371px) and (max-width: 477px) {
  .div {
    position: relative;
    width: 100%;
    height: auto;
    background-color: #FFF;
    text-align: center; }
    .div .form {
      text-align: center;
      width: 100%; }
      .div .form .SignUpButton {
        height: 30px;
        width: 100%; } }

@media only screen and (max-width: 370px) {
  .div {
    position: relative;
    width: 100%;
    height: auto;
    background-color: #FFF;
    text-align: center; }
    .div .form {
      text-align: center;
      width: 100%; }
      .div .form .SignUpButton {
        height: 25px;
        width: 100%; } }



