:global body {
  background-color: #F0F0F0;
  overflow-x: hidden; }

.button {
  background-color: #00ff66;
  margin-right: 5px;
  float: left;
  font-size: 9px;
  width: 50px;
  height: 25px; }

@media only screen and (min-width: 768px) {
  .button {
    background-color: #00ff66;
    margin-right: 5px;
    font-size: 14px;
    width: 80px;
    height: 35px;
    float: left; } }

.div {
  position: relative;
  left: 40%; }



