:global body {
  background-color: #F0F0F0;
  overflow-x: hidden; }

.user:after {
  display: block;
  float: left; }

.user .avatar {
  border-radius: 5px;
  height: 30px;
  width: auto;
  float: left;
  margin-right: 10px; }

.user .details {
  font-weight: bold; }

.timeStamp {
  float: right; }

.actions {
  position: relative;
  margin-bottom: 10px; }
  .actions .icon {
    color: #999;
    margin-right: 20px;
    width: 5px;
    height: 5px; }
  .actions .goldIcon {
    color: yellow;
    margin-right: 20px;
    width: 5px;
    height: 5px; }

@media only screen and (min-width: 768px) {
  .story {
    border: 1px solid #ccc;
    padding: 25px 50px;
    border-radius: 5px;
    width: 100%;
    height: 100%;
    background-color: #FFF; }
  .content {
    margin: 30px 0;
    font-family: georgin;
    font-size: 1.1em;
    width: 100%; } }

@media only screen and (min-width: 478px) and (max-width: 767px) {
