    border: 1px solid #ccc;
    padding: 15px;
    padding-bottom: 5px;
    border-radius: 5px;
    width: 100%;
    height: 100%;
    background-color: #FFF; }
  .content {
    margin: 30px 0;
    font-family: georgin;
    font-size: 1em;
    width: 100%; } }

