:global body {
  background-color: #F0F0F0;
  overflow-x: hidden; }

@media only screen and (min-width: 768px) {
  .div {
    position: relative;
    width: 60%; } }

@media only screen and (min-width: 478px) and (max-width: 767px) {
  .div {
    position: relative;
    width: 90%; } }

@media only screen and (min-width: 371px) and (max-width: 477px) {
  .div {
    position: relative;
    width: 90%; } }

@media only screen and (max-width: 370px) {
  .div {
    width: 100%; } }



