:global body {
  background-color: #F0F0F0;
  overflow-x: hidden; }

@media only screen and (min-width: 768px) {
  .header {
    position: relative;
    text-align: center;
    width: 100%; }
    .header .img {
      width: 100%;
      height: auto; }
    .header .introduction {
      position: absolute;
      top: 15%;
      left: 15%;
      width: 30%; }
      .header .introduction .largeText {
        font-size: 36px;
        color: white; }
      .header .introduction .mediumText {
        font-size: 24px;
        color: white; }
      .header .introduction .smallText {
        font-size: 18px;
        color: white; } }

