:global body {
  background-color: #F0F0F0;
  overflow-x: hidden; }

@media only screen and (min-width: 768px) {
  .buttonsDiv {
    position: relative;
    left: 20%;
    width: 60%; }
  .div {
    width: 50%; }
  .textarea {
    position: relative;
    width: 80%;
    height: 150px;
    resize: none; } }

@media only screen and (min-width: 478px) and (max-width: 767px) {
  .buttonsDiv {
    position: relative;
    left: 5%;
    width: 90%; }
  .textarea {
    position: relative;
    width: 90%;
    height: 150px;
    resize: none; } }

@media only screen and (min-width: 371px) and (max-width: 477px) {
  .buttonsDiv {
    position: relative;
    left: 5%;
    width: 90%; }
  .textarea {
    position: relative;
    width: 90%;
    height: 150px;
    resize: none; } }

@media only screen and (max-width: 370px) {
  .buttonsDiv {
    width: 100%; }
  .textarea {
    position: relative;
    width: 100%;
    height: 150px;
    resize: none; } }

.button {
  float: right;
  height: 30px;
  margin-right: 5px;
  font-size: 12px;
  text-align: center;
  background-color: #00ff66; }



