:global body {
  background-color: #F0F0F0;
  overflow-x: hidden; }

@media only screen and (min-width: 768px) {
  .input {
    margin-right: 10px;
    text-align: center;
    height: 40px;
    width: 100%; } }

@media only screen and (min-width: 478px) and (max-width: 767px) {
  .input {
    margin-right: 10px;
    text-align: center;
    height: 40px;
    width: 100%; } }

@media only screen and (min-width: 371px) and (max-width: 477px) {
  .input {
    margin-right: 10px;
    text-align: center;
    height: 30px;
    width: 100%; } }

@media only screen and (max-width: 370px) {
  .input {
    margin-right: 10px;
    text-align: center;
    height: 25px;
    width: 100%; } }



