:global body {
  background-color: #F0F0F0;
  overflow-x: hidden; }

.newPostSuccessDiv {
  position: fixed;
  bottom: 5%;
  right: 50%;
  font-size: 20px;
  color: #00ff66; }

.newPostErrorDiv {
  position: fixed;
  bottom: 5%;
  right: 50%;
  font-size: 20px;
  color: red; }

.newPostImg {
  position: fixed;
  bottom: 10%;
  right: 10%;
  z-index: 1; }

.NewPostShow {
  position: fixed;
  width: 100%;
  z-index: 1;
  background-color: rgba(0, 0, 0, 0.5);
  height: 100%; }

.NewPostHide {
  position: fixed;
  top: 100%;
  width: 100%;
  z-index: -1; }



