:global body {
  background-color: #F0F0F0;
  overflow-x: hidden; }

@media only screen and (min-width: 768px) {
  nav {
    height: auto;
    margin-bottom: 0px;
    border-radius: 0;
    background-color: #2d3138;
    color: #FFF;
    width: 100%;
    margin: 0;
    font-size: 1em;
    border: 0;
    padding: 0px; }
    nav div div a {
      position: relative;
      margin: 10px;
      float: left; }
    nav div ul li #loginBtn {
      display: none; } }

@media only screen and (max-width: 767px) {
  nav {
    height: auto;
    margin-bottom: 0px;
    border-radius: 0;
    background-color: #2d3138;
    color: #FFF;
    width: 100%;
    margin: 0;
    font-size: 1em;
    border: 0;
    padding: 0px; }
    nav div div a {
      position: relative;
      margin: 10px;
      float: left; }
      nav div div a img {
        width: 75%;
        height: auto; }
    nav div ul li #loginBtn {
      float: right; }
      nav div ul li #loginBtn:hover {
        background-color: lightgrey; }
    nav div .input {
      display: none; } }



