:global body {
  background-color: #F0F0F0;
  overflow-x: hidden; }

@media only screen and (min-width: 768px) {
  .div {
    margin-top: 100px;
    width: 60%; }
  .img {
    position: relative;
    float: left;
    width: 40%;
    height: auto; }
  .FollowButton {
    float: left;
    background-color: #00ff66; } }

@media only screen and (min-width: 478px) and (max-width: 767px) {
  .div {
    margin-top: 100px;
    width: 80%; }
  .img {
    position: relative;
    float: left;
    width: 50%;
    height: auto; }
  .FollowButton {
    float: left;
    background-color: #00ff66; } }

@media only screen and (min-width: 371px) and (max-width: 477px) {
  .div {
    margin-top: 10px;
    width: 100%; }
  .img {
    position: relative;
    float: left;
    width: 50%;
    height: auto; }
  .FollowButton {
    float: left;
    background-color: #00ff66; } }

@media only screen and (max-width: 370px) {
  .div {
    margin-top: 10px;
    width: 100%; }
  .img {
    position: relative;
    float: left;
    width: 50%;
    height: auto; }
  .FollowButton {
    float: left;
    width: 50px;
    height: 25px;
    font-size: 10px;
    padding: 3px;
    background-color: #00ff66; } }



