:global body {
  background-color: #F0F0F0;
  overflow-x: hidden; }

.navbar {
  background-color: #2d3138;
  border-radius: 0; }

.img {
  margin-top: 10px;
  margin-right: 20px;
  margin-left: 20px; }

@media only screen and (min-width: 768px) {
  .logo {
    position: relative;
    float: left; } }

@media only screen and (max-width: 767px) {
  .logo {
    display: none; } }



