:global body {
  background-color: #F0F0F0;
  overflow-x: hidden; }

.btn {
  background-color: #01a2aa; }

.postsButton {
  margin-right: 10px;
  float: left;
  width: 50px;
  height: 25px;
  font-size: 14px;
  color: white;
  padding: 3px; }

.newPostButton {
  margin-right: 10px;
  float: left;
  width: 50px;
  height: 25px;
  font-size: 14px;
  background-color: #2d3138;
  padding: 3px;
  color: white; }

.buttonsDiv {
  position: relative;
  left: 40%; }

@media only screen and (min-width: 768px) {
  .postsDiv {
    width: 100%; }
  .postsButton {
    margin-right: 10px;
    float: left;
    width: 80px;
    height: 35px;
    font-size: 14px;
    color: white;
    padding: 3px; }
  .newPostButton {
    margin-right: 10px;
    float: left;
    width: 80px;
    height: 35px;
    font-size: 14px;
    padding: 3px;
    color: white; } }



